body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-link:hover { 
  color: blue !important;
}

h5{
  color: rgb(68,116,193);
}

.fa.fa-linkedin {
  border: none;
  padding: 10px;
  font-size: 30px;
  width: 45px;
  height: 45px;
  text-align: center;
  text-decoration: none;
  /*border-radius: 50%;*/
}

.fa:hover {
    opacity: 0.7;
}

.fa-linkedin {
  background: #007bb5;
  color: white;
}

.footer {
  /*position: fixed;
  left: 0;
  bottom: 0;
  */
  width: 100%;
  background-color: #555555;
  color: white;
  text-align: center;
}

.footerStatic {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #555555;
  color: white;
  text-align: center;
}

.short-text { display: none; }

/* When resolution <= 1200px, hide short text and show full text  */
@media (max-width: 1200px) {
    .short-text { display: inline-block; }
    .full-text { display: none; }
}
/* When resolution <= 990px, use footer relative instead of static  */
@media (max-width: 990px) {
  .footerStatic { position: relative; }
}
#honeypot {
  display: none; /*makes the field not visible to humans*/
}
